.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 120px;
    background-color: darkcyan;
    color: white;
}

.header nav {
    display: flex;
    align-items: center;
}

.header nav a {
    margin: 20px;
    text-decoration: none;
    color: white;
}

.header nav a:hover {
    color: darkslateblue;
}

.logo-header {
    margin-top: 15px;
    width: 300px;
}