.Book {
    display: flex;
    justify-content: center;
}

.book-title {
    display: flex;
    margin-top: 50px;
    width: 100px;
    height: 50px;
    color: darkslategrey;
    font-size: 30px;

}

.book-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    flex-direction: row;
    width: 80%;
}

.book-wrapper {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 10%;
}

.book-img {
    display: flex;
    width: 200px;
}

.buy-info {
    display: flex;
    flex-direction: row;
    margin-top: 15px;
    justify-content: space-between;
}

.price {
    display: flex;
    font-size: 20px;
    margin-top: 5px;
    font-weight: bold;
}

.buy-info .img-cart {
    display: flex;
    width: 30px;
    flex-direction: row;
}

.buy-info .img-heart {
    display: flex;
    width: 30px;
    flex-direction: row;
}
